import * as React from 'react'
import styled from '../styles/styled-components'

const StyledContainer = styled.section`
  position: relative;
  width: 100%;
  padding-top: 54.6875%;
`

const Contents = styled.div`
  position: absolute;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
`

interface CardContainerProps {
  className?: string
}

export const RatioContainer: React.SFC<CardContainerProps> = ({ className, children }) => (
  <StyledContainer>
    <Contents className={className}>{children}</Contents>
  </StyledContainer>
)



