import * as React from 'react'
import styled from '../styles/styled-components'
import { RatioContainer } from './RatioContainer'
import { BackgroundImage } from './BackgroundImage'

const IntroContainer = styled(RatioContainer)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: ${p => p.theme.dimensions.containerPadding}rem;
`

const Contents = styled.div`
  max-width: 786px;

  p {
    font-size: 1.75rem;
    line-height: 1.1786;
    letter-spacing: 0.38px;
  }
`

interface IntroProps {
  className?: string
  bgImage: SharpSizesImage
  header: React.ReactNode
  text: React.ReactNode
}

export const Intro: React.SFC<IntroProps> = ({ className, bgImage, header, text }) => (
  <IntroContainer>
    <BackgroundImage image={bgImage} />
    <Contents className={className}>
      <h1>{header}</h1>
      <p>{text}</p>
    </Contents>
  </IntroContainer>
)



